* {
    margin: 0;
}

.view {
    width: 190px;
    height: 100px;
    background-color: rgb(61, 121, 121);
    color: black;
    display: flex;
    justify-content: center;
    align-content: center;
    font-family: sans-serif;
    border-radius: 20px;
    margin: 20px;
}

.mainContainer {
    display: block;
    justify-content: center;
    align-content: center;
    width: 190px;
}

.title {
    display: flex;
    justify-content: center;
    align-content: center;
    width: 180px;
    height: 50px;
    margin-top: 10px;
    margin-bottom: 0;
}