.item {
    width: 400px;
    height: 120px;
    display: flex;
    justify-content: center;
    align-content: center;
    margin: 20px;
    border-radius: 20px;
    background-color: rgb(1, 1, 52);
    color: white;
}

.icon {
    width: 80px;
    height: 80px;
    border-radius: 100px;
    margin-right: 15px;
    margin: 20px;
}

img {
    width: 80px;
    height: 80px;
    border-radius: 100px;
}

.content {
    font-family: sans-serif;
    margin-top: 20px;
}